#bookings {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  #bookings tr:last-child td {
    height: 0;
    background: transparent; }
  #bookings th {
    width: 35px;
    height: 35px;
    text-align: center;
    background: #d8d8d8;
    padding: 5px;
    border-right: 4px solid white;
    border-bottom: 2px solid white; }
    #bookings th.weekend {
      background-color: #cca9cc; }
    #bookings th:last-child {
      border-right: 0; }
    #bookings th.empty {
      border-bottom: 0 !important; }
    #bookings th.empty-date {
      border-right-width: 2px; }
  #bookings td {
    width: 35px;
    height: 35px;
    text-align: center;
    background: #eee;
    border-bottom: 1px solid white; }
    #bookings td.weekend {
      background-color: #dfc9df; }
    #bookings td.day-left {
      border-right: 2px solid white; }
    #bookings td.day-right {
      border-right: 4px solid white; }
    #bookings td.booking {
      background: #2ad62a;
      cursor: pointer;
      text-align: left;
      padding-left: 3px;
      border-right: 2px solid white;
      overflow: hidden;
      white-space: nowrap; }
      #bookings td.booking div {
        width: 0px; }
    #bookings td.selected {
      border-left: 0;
      border-right: 0;
      background-color: #f4efc8; }
      #bookings td.selected.booking {
        background-color: #92e366;
        border-right: 2px solid white; }
    #bookings td.select-end {
      border-right: 2px solid white; }
  #bookings .item-name, #bookings th.empty {
    border-right: 2px solid white;
    width: 100px; }



