#info {
  background: #eee;
  padding: 8px;
  max-width: 400px;
  margin-bottom: 10px; }
  #info .row {
    -ms-flex-align: end;
        align-items: flex-end; }
  #info .group {
    padding: 0 10px 10px 10px;
    border: 1px solid #aaa;
    margin-top: 10px; }
    #info .group legend {
      width: auto;
      padding: 0 5px;
      margin: 0;
      font-size: 12pt;
      background: #ddd;
      border: 1px solid #aaa; }
  #info label {
    width: 100%; }
  #info input#info-nights, #info label[for=info-nights] {
    text-align: center; }
  #info input.error {
    border-color: #e2a3a3; }
  #info button {
    margin-top: 5px; }
  #info .icon.cancel {
    color: red;
    margin-top: -2px; }
  #info .options {
    padding: 3px 6px; }
  #info .choose-customer {
    font-size: 10pt;
    padding: 6pt 9pt; }



